* {
  box-sizing: border-box;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
}

body {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: start;
  height: 100vh;
  background: url('../../assets/img/bg.jpg');
  background-attachment: fixed;
  background-size: contain;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
  border-radius: 3px;
}

.page-container {
  min-height: 100vh;
  ::-webkit-scrollbar {
    width: 8px;
  }
  ::-webkit-scrollbar-track {
    background: #091e4214;
    border-radius: 50rem;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 50rem;
    background: #091e4214;
  }
}

button,
a,
input,
option,
select,
.task,
svg,
.group-header {
  cursor: pointer;
}

button {
  border: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

textarea {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #fff;
}

input:focus {
  outline-color: #2684ff;
}

.card {
  border-radius: 3px;
}

.close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  border-radius: 20px;
}

.close-button:hover {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  background-color: rgba(116, 92, 92, 0.338);
}

.MuiTypography-root {
  width: 100%;
}
